* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body,
html {
  padding: 0;
  border: 0;
  vertical-align: baseline;
  min-height: 100%;
  width: 100%;
  overflow-x: hidden;
}
html {
  font-size: 16px;
}
@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}
body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  min-height: 100%;
  color: #222;
  background-color: #fff;
  line-height: 1;
  scroll-behavior: smooth;
}
.browsehappy {
  display: block;
  margin: 0;
  padding: 30px;
  text-align: center;
  background: #b71c1c;
  color: #fff;
  font-size: 1.125rem;
  line-height: 1.375rem;
}
.browsehappy a,
.browsehappy a:hover {
  color: #fff;
  text-decoration: underline;
}
.wrapper {
  min-height: 100vh;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}
.main {
  flex: 1 0 auto;
}
.footer {
  flex: 0 0 auto;
}
a {
  color: inherit;
  text-decoration: none;
  transition: all 0.25s linear;
}
a:hover {
  color: #765bb2;
  text-decoration: none;
  border-bottom-color: #765bb2;
}
a:focus,
button:focus,
input:focus {
  outline: none;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
input,
textarea,
button,
select {
  outline: none;
  border: 0;
  color: inherit;
  font-family: inherit;
  background: transparent;
  transition: all 0.25s linear;
}
button,
button[type="submit"],
input[type="submit"] {
  cursor: pointer;
}
input,
textarea {
  line-height: 1;
  border: 0;
  background-color: transparent;
  font-family: inherit;
}
input::placeholder,
textarea::placeholder {
  color: #999;
}
input[type="search"] {
  -webkit-appearance: none;
}
textarea {
  width: 100%;
  resize: none;
}
fieldset {
  border: none;
  margin: 0;
  padding: 0;
}
.hide-input,
.hide {
  display: none;
}
.hidden-input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}
.button,
.button--round {
  display: inline-block;
  text-transform: uppercase;
  font-weight: 500;
  text-align: center;
  line-height: 1.2;
  padding: 15px 25px 12px;
  transition: all 0.2s linear;
}
.button--round,
.button--round--round {
  border-radius: 50px;
}
.button--base,
.button--round--base {
  background-color: #765bb2;
  color: #fff;
}
.button--base:hover,
.button--round--base:hover {
  color: #fff;
  background-color: #8b74be;
}
.button--white,
.button--round--white {
  background-color: #fff;
  color: #222;
}
.button--white:hover,
.button--round--white:hover {
  background-color: #765bb2;
  color: #fff;
}
.cur {
  font-size: 0.85em;
}
.menu-icon {
  width: 45px;
  padding: 10px 10px 5px;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10;
  display: none;
  background-color: #fff;
  transition: all 0.2s linear;
}
@media (max-width: 992px) {
  .menu-icon {
    display: block;
  }
}
.menu-icon span {
  display: block;
  height: 4px;
  background-color: #765bb2;
  border-radius: 2px;
  margin-bottom: 4px;
  transition: all 0.25s linear;
}
.menu-icon.active {
  transform: rotate(90deg);
  background-color: transparent;
}
.menu-icon.active span {
  background-color: #fff;
}
.b-logotype img {
  max-width: 100%;
  height: auto;
}
.b-worktime {
  display: inline-block;
}
.b-worktime--inline .icon {
  width: 13px;
  height: 13px;
  fill: #f4f4f4;
  margin-right: 5px;
  vertical-align: baseline;
}
.b-worktime--inline strong {
  text-transform: uppercase;
  color: #fff;
}
.b-worktime--block {
  position: relative;
  font-size: 1.125rem;
  padding-left: 45px;
  line-height: 1.875rem;
}
@media (max-width: 1200px) {
  .b-worktime--block {
    font-size: 0.9375rem;
  }
}
.b-worktime--block .icon {
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -14px;
}
.b-phones {
  display: inline-block;
  text-align: right;
}
.b-phones__item {
  position: relative;
}
.b-phones--block {
  position: relative;
  line-height: 1.875rem;
  font-weight: 700;
  font-size: 1.125rem;
}
@media (max-width: 1200px) {
  .b-phones--block {
    font-size: 0.9375rem;
  }
}
@media (max-width: 544px) {
  .b-phones--block {
    padding-left: 0;
  }
}
.b-phones--block a {
  display: inline-block;
  color: #765bb2;
}
.b-phones--block a:hover {
  color: #c1adeb;
}
.b-phones--inline .icon {
  width: 13px;
  height: 13px;
  fill: #fff;
  margin-right: 5px;
}
.b-phones--inline strong {
  display: block;
  text-align: left;
  text-transform: uppercase;
  color: #fff;
}
.b-title {
  font-size: 2.25rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #393939;
  margin-bottom: 30px;
}
@media (max-width: 992px) {
  .b-title {
    margin-bottom: 15px;
    font-size: 1.3rem;
    line-height: 1.2;
